import { CameraProvider } from '../../providers/util/camera.provider';
import { Component, Injectable } from '@angular/core';
import {
  NavController,
  Platform,
  ActionSheetController,
  LoadingController,
  IonicPage,
  NavParams } from 'ionic-angular';

import { ProfilePage } from '../profile/profile';

import { Product } from '../../models/product/product.interface';
import { Http, Headers, RequestOptions } from '@angular/http';

import { Localstorage } from '../../providers/localstorage';

import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';

import { ImageResizer, ImageResizerOptions } from '@ionic-native/image-resizer';

// imagem base64
import { Base64 } from '@ionic-native/base64';

@IonicPage()
@Component({
  selector: 'page-add-work',
  templateUrl: 'add-work.html',
})
export class AddWorkPage {

  product = {} as Product;

  placeholder = 'assets/img/avatar/girl-avatar.png';
  chosenPicture: any;
  imagem64;

  constructor(
    public navCtrl: NavController,
    public actionsheetCtrl: ActionSheetController,
    public cameraProvider: CameraProvider,
    public platform: Platform,
    public loadingCtrl: LoadingController,
    public navParams: NavParams,
    private http: Http,
    public localstorage:Localstorage,
    private base64: Base64,
    private imageResizer: ImageResizer
  ) {

    this.localstorage = localstorage;

  }

  changePicture() {
    
    const actionsheet = this.actionsheetCtrl.create({
      title: 'upload picture',
      buttons: [
        {
          text: 'camera',
          icon: !this.platform.is('ios') ? 'camera' : null,
          handler: () => {
            this.takePicture();
          }
        },
        {
          text: !this.platform.is('ios') ? 'gallery' : 'camera roll',
          icon: !this.platform.is('ios') ? 'image' : null,
          handler: () => {
            this.getPicture();
          }
        },
        {
          text: 'cancel',
          icon: !this.platform.is('ios') ? 'close' : null,
          role: 'destructive',
          handler: () => {
            console.log('the user has cancelled the interaction.');
          }
        }
      ]
    });
    return actionsheet.present();
  }
    
  takePicture() {
    const loading = this.loadingCtrl.create();
    
    loading.present();
    return this.cameraProvider.getPictureFromCamera().then(picture => {
      if (picture) {
        this.chosenPicture = picture;
      }
      loading.dismiss();
    }, error => {
      alert(error);
    });
  }
    
  getPicture() {
    const loading = this.loadingCtrl.create();
    
    loading.present();
    return this.cameraProvider.getPictureFromPhotoLibrary().then(picture => {
      if (picture) {
        this.chosenPicture = picture;
      }
      loading.dismiss();
    }, error => {
      alert(error);
    });
  }

  posts = [
    {
      postImageUrl: 'assets/img/background/background-2.jpg',
      title: 'Título do Produto',
      text: `I believe in being strong when everything seems to be going wrong.
             I believe that happy girls are the prettiest girls.
             I believe that tomorrow is another day and I believe in miracles.`,
      date: 'November 5, 2016',
      likes: 12,
      comments: 4
    },
  ];

  addProduto(product : Product):void {

        //let filePath: string = this.chosenPicture;
        let filePath: string = this.chosenPicture;
        this.base64.encodeFile(filePath).then((base64File: string) => {
          this.imagem64 = base64File;

          // tslint:disable-next-line:no-var-keyword
        var headers = new Headers();
        headers.append('Accept', 'application/json');
        headers.append('Content-Type', 'application/json' );

      
        // tslint:disable-next-line:prefer-const
        let options = new RequestOptions({ headers: headers });

        this.localstorage.getUser('ts').then((dados) => {
          // tslint:disable-next-line:prefer-const
          let res = dados;

          // tslint:disable-next-line:prefer-const
          let data = JSON.stringify({
            productTitle: product.title,
            productorID: res._id,
            productorName: res.name,
            productCategory: product.categoria,
            productDescription: product.description,
            productImage: this.imagem64
          });

          console.log('+++++++++++++++++');
          console.log(data);
          console.log('+++++++++++++++++');
              
          new Promise((resolve, reject) => {
            this.http.post('https://imagine-art.herokuapp.com/product/newproduct/',data, options)
                .toPromise()
                .then((response) => {
                  
                  if (response.json().code === 200) {
          
                      // IMPORTAR PAGINA PARA IR PARA O PROFILE PAGE AQUI
                    console.log('----------------');
                    console.log(response.json());
                    console.log('----------------');
                    this.navCtrl.push(ProfilePage);
                    
                    
                  }
                  console.log(response.json());
                  product = {} as Product;
          
                  resolve(response.json());
                  
                })
                .catch((error) => {
                  console.error('API Error : ', error.status);
                  console.error('API Error : ', JSON.stringify(error));
                  reject(error.json());
                });
          });

        })
          .catch((err) => {
            console.log('Error occurred :', err);
          });


        }, (err) => {
          console.log(err);
        });
      
  }
  
}



